

































import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			bgimgsrc: 'http://hashcredit.io/html/img/loginbg.png',
			submit_status: true,
			userInfo: '',
			mobile: '',
			verifyCode: '',
			encryptedData: '',
			iv: '',
			code: '',
			losetime: 0,
		};
	},
	computed: {
		...mapState({ //这里的...不是省略号了,是对象扩展符
			$globalData: 'globalData'
		})
	},
	onLoad: function() {
		let that = this;
		this.getUserInfofs(function() {
			that.submit_status = false;
		});
	},
	methods: {
		getUserInfofs: function(callback) {
			let that = this;
			wx.getUserInfo({
				success: function(res) {
					console.log('已授权获取用户信息', res);
					that.encryptedData = res.encryptedData;
					that.iv = res.iv;
					that.userInfo = res.userInfo;
					wx.login({
						success: function(res) {
							console.log(res)
							that.code = res.code; //登录凭证
							console.log('code码', res.code);
							callback();
						}
					})
				}
			});
		},
		bindLogin() {
			let that = this;
			if (this.mobile.length < 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号输入错误'
				});
				return;
			}
			if (this.verifyCode.length < 4) {
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				});
				return;
			}
			let register_data = {
				code: this.code,
				mobile: this.mobile,
				verifyCode: this.verifyCode,
				encryptedData: this.encryptedData,
				iv: this.iv,
				parentCode: this.$store.state.shareFriendCode
			}
			console.log(register_data);
			wx.request({
				url: this.$globalData.urlPath + '/app/wx/xcxRegistlogin',
				data: register_data,
				method: 'POST',
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					console.log(res)
					if (res.data.status == 1) {
						that.$store.commit('set_userCode', res.data.userCode);
						that.$store.commit('set_invitationCode', res.data.invitationCode);
						that.$store.commit('set_token', res.data.token);
						that.$store.commit('set_score', res.data.score);
						uni.showToast({
							icon: 'success',
							title: '注册成功',
							success: function() {
								uni.switchTab({
									url: '../home/home'
								})
							}
						});
					} else if (res.data.status == 0) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: '注册失败'
						});
					}
				},
				fail: function(err) {
					console.log('err', err)
				}
			});
		},
		getVerifyCode() {
			let that = this;
			let time = new Date().getTime();
			if (this.losetime > time) {
				let t = parseInt((this.losetime - time) / 1000);
				uni.showToast({
					icon: 'none',
					title: '请等待' + t + '秒后重发'
				});
				return;
			}
			if (this.mobile.length < 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号输入错误'
				});
				return;
			}
			let data = {
				mobile: this.mobile
			}
			this.$api.sendVerifyCode(data, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					let newtime = new Date().getTime();
					that.losetime = newtime + 30000;
					uni.showToast({
						icon: 'success',
						title: '验证码已发送'
					});
				}
			}, function(err) {
				console.log(err);
			});
		}
	},
}



